$headerText: rgb(236, 235, 191);
$money: #30663b;
$onHover: rgb(204, 177, 109);

.noBackground th{
  background: none;
}



.product {
  color: $headerText;
  text-decoration: none;
  left: 3px;
  right: 3px;

}

.product:hover {

  text-shadow: 2px 2px 2px $money;
  font-weight: bold;
  color: $onHover;
  cursor: pointer;
}



