.rightLinks li{margin-left:20px}.logo a{color:#a3863c;font-weight:bold}.rightLinks a{color:#cfceaa}.logo a:hover{color:#a3863c;font-weight:bold;text-shadow:2px 2px 2px #215050}.emojis{opacity:0.6}.tallLetter{font-size:large;font-weight:bold}ul{list-style-type:none;margin:0px;text-decoration:none;text-decoration:none;padding:20px 20px;display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;background-color:#242420;box-shadow:0px 1px 3px #998961}li{text-decoration:none;float:left}

.App{text-align:center;text-decoration:none}a{color:#ecebbf;text-decoration:none;left:3px;right:3px}a:hover{text-shadow:2px 2px 2px #30663b;font-weight:bold;color:#ccb16d;cursor:pointer}button,input{color:#a0a061;background-color:#3a3a34;padding:5px 10px;border-radius:4px;margin:5px 10px;border-color:#3a3a34;box-shadow:2px 2px 2px #272723;outline:none}button:hover,input:hover{background-color:#42423c;box-shadow:2px 2px 2px #ccb16d;cursor:pointer}button:hover{transform:translateY(4px);transform:translateX(4px)}button:active{color:#ccb16d}button:active:after{color:#ccb16d}table{margin-left:auto;margin-right:auto;padding:2rem;border-collapse:collapse;background-color:#3a3a34;margin-top:2rem}th{background-color:#42423c;padding:5px 10px;color:#a0a061;border:1px solid #777763}td{padding:5px 10px;color:#a0a061;border:1px solid #777763}tr:hover{background-color:#4d4d41}.rowPrimary{background-color:#55554c}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

